import {Component, OnInit} from '@angular/core';

import {Platform} from '@ionic/angular';
import {SplashScreen} from '@ionic-native/splash-screen/ngx';
import {StatusBar} from '@ionic-native/status-bar/ngx';
import {ControllerService} from './services/controller.service';
import {PRIVILEGES} from './services/constants.service';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html'
})
export class AppComponent implements OnInit {

  private userMenus = [
    {
      title: 'Shop',
      url: '/home',
        icon: 'business',
      action: () => {},
    },
    {
      title: 'Cart',
      url: '/cart',
      icon: 'cart',
      action: () => {},
    },
    {
      title: 'Orders',
      url: '/my-orders',
      icon: 'basket',
      action: () => {},
    },
  ];

  private adminMenus = [
    {
      title: 'Admin Home',
      url: '/admin-home',
      icon: 'home',
      action: () => {},
    },
    {
      title: 'Add Product',
      url: '/admin-add-product',
      icon: 'add-circle',
      action: () => {},
    },
    {
      title: 'Products',
      url: '/admin-products',
      icon: 'cube',
      action: () => {},
    },
    {
      title: 'Orders',
      url: '/admin-orders',
      icon: 'basket',
      action: () => {},
    },
    {
      title: 'Logout',
      url: '.',
      icon: 'log-out',
      action: this.adminLogout.bind(this),
    }
  ];
  public appPages: {title: string, url: string, icon: string, action: Function}[];
  currentUser: string;

  constructor(
    private platform: Platform,
    private splashScreen: SplashScreen,
    private statusBar: StatusBar,
    private api: ControllerService
  ) {
    this.initializeApp();
  }

  ngOnInit(): void {
    this.currentUser = this.api.getCurrentUser();
    switch (this.currentUser) {
      case PRIVILEGES.USER:
        this.appPages = this.userMenus;
        break;
      case PRIVILEGES.ADMIN:
        this.appPages = this.adminMenus;
        break;
      default:
        this.appPages = this.userMenus;
    }
  }

  adminLogout() {
    this.api.logout(PRIVILEGES.ADMIN);
    location.assign('/home');
  }

  initializeApp() {
    this.platform.ready().then(() => {
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }
}
