import { Injectable } from '@angular/core';


export enum STORAGE_KEYS {
  USERS = 'store_users',
  ADMIN = 'store_admin',
  ORDERS = 'store_orders',
  CART = 'store_cart',
  ITEMS = 'store_items',
  USER_LOGIN = 'store_user_login',
  ADMIN_LOGIN = 'store_admin_login',
  CURRENT_USER = 'store_user',
}

export enum DB_DATATYPES {
  OBJECT = 'object',
  ARRAY = 'array',
}

export enum PRIVILEGES {
  USER = 'user',
  ADMIN = 'admin',
}

@Injectable({
  providedIn: 'root'
})
export class ConstantsService {

  constructor() { }

  formatCurrency(val: string) {
    val = Number(val).toFixed(2);
    const values = val.split('.');
    const valLength = values[0].length;
    const a = (valLength / 3);
    let b = Math.floor(valLength / 3);

    if ((a >= 1 && a > b) || (a >= 1 && a === b)) {
      if (a === b) {
        b -= 1;
      }
      let end = valLength;
      let start = 0;
      let str = '';
      for (let x = 0; x < b; x += 1) {
        start = end - 3;
        str = `,${values[0].substring(start, end)}${str}`;
        end -= 3;
      }
      if (b > 0) {
        values[0] = values[0].substring(0, start) + str;
      }
    }

    return values.join('.');
  }
}
