import {Injectable} from '@angular/core';
import {StorageService} from './storage.service';
import {IdGeneratorService} from './id-generator.service';
import {DB_DATATYPES, PRIVILEGES, STORAGE_KEYS} from './constants.service';

@Injectable({
  providedIn: 'root'
})
export class ControllerService {

  constructor(private idGen: IdGeneratorService) {
    StorageService.init();
  }

  // signUp(name: string, matric: string, phone: string, password: string): boolean {
  //   const user = { name, matric, phone, password, orders: [] };
  //   StorageService.insert(DB_DATATYPES.OBJECT, STORAGE_KEYS.USERS, user, matric);
  //   return this.login(matric, password);
  // }

  login(username: string, password: string, privilege: PRIVILEGES = PRIVILEGES.USER): boolean {
    this.logout();
    switch (privilege) {
      case PRIVILEGES.USER:
        const userData = StorageService.select(STORAGE_KEYS.USERS, username);
        if (userData === false) {
          return false;
        } else {
          if (password !== userData.password) {
            return false;
          } else {
            StorageService.set(STORAGE_KEYS.USER_LOGIN, userData);
            StorageService.set(STORAGE_KEYS.CURRENT_USER, PRIVILEGES.USER);
            return true;
          }
        }
      case PRIVILEGES.ADMIN:
        const adminData = StorageService.get(STORAGE_KEYS.ADMIN);
        if (adminData === false) {
          return false;
        } else {
          if (password !== adminData.password) {
            return false;
          } else {
            StorageService.set(STORAGE_KEYS.ADMIN_LOGIN, adminData);
            StorageService.set(STORAGE_KEYS.CURRENT_USER, PRIVILEGES.ADMIN);
            return true;
          }
        }
      default:
    }
  }

  logout(privilege: PRIVILEGES = PRIVILEGES.USER) {
    if (privilege === PRIVILEGES.USER) {
      StorageService.drop(STORAGE_KEYS.USER_LOGIN);
      StorageService.drop(STORAGE_KEYS.CURRENT_USER);
      StorageService.drop(STORAGE_KEYS.CART);
    } else if (privilege === PRIVILEGES.ADMIN) {
      StorageService.drop(STORAGE_KEYS.ADMIN_LOGIN);
      StorageService.drop(STORAGE_KEYS.CURRENT_USER);
    }
  }

  // getProfile() {
  //   return StorageService.get(STORAGE_KEYS.USER_LOGIN);
  // }

  getCurrentUser() {
    return StorageService.get(STORAGE_KEYS.CURRENT_USER);
  }

  getProducts() {
    return StorageService.get(STORAGE_KEYS.ITEMS);
  }

  getCart() {
    return StorageService.get(STORAGE_KEYS.CART);
  }

  resetCart() {
    StorageService.set(STORAGE_KEYS.CART, []);
  }

  // getUserOrders(orderIds: string[]) {
  //   const orders = {};
  //   orderIds.forEach((id) => {
  //     orders[id] = StorageService.select(STORAGE_KEYS.ORDERS, id);
  //   });
  //   return Object.values(orders);
  // }

  getOrders() {
    return StorageService.get(STORAGE_KEYS.ORDERS);
  }

  getOrderById(id: string) {
    return StorageService.select(STORAGE_KEYS.ORDERS, id);
  }

  addProduct(product: any) {
    product.id = this.idGen.generate();
    return StorageService.insert(DB_DATATYPES.OBJECT, STORAGE_KEYS.ITEMS, product, product.id);
  }

  deleteProduct(id: string) {
    return StorageService.delete(DB_DATATYPES.OBJECT, STORAGE_KEYS.ITEMS, id);
  }

  addToCart(item: any) {
    return StorageService.insert(DB_DATATYPES.ARRAY, STORAGE_KEYS.CART, item);
  }

  updateCart(cart: any) {
    return StorageService.set(STORAGE_KEYS.CART, cart);
  }

  createOrder(order: any) {
    const id = this.idGen.generate();
    order.id = id;
    StorageService.insert(DB_DATATYPES.OBJECT, STORAGE_KEYS.ORDERS, order, id);
    return order;
  }

  // updateProfile(data: any, id: string) {
  //   StorageService.insert(DB_DATATYPES.OBJECT, STORAGE_KEYS.USERS, data, id);
  //   StorageService.set(STORAGE_KEYS.USER_LOGIN, data);
  // }

  deleteOrder(id: string) {
    StorageService.delete(DB_DATATYPES.OBJECT, STORAGE_KEYS.ORDERS, id);
    // const user = this.getProfile();
    // const orderIndex = user.orders.indexOf(id);
    // user.orders.splice(orderIndex, 1);
    // this.updateProfile(user, user.matric);
  }

  makePay(order: any, status: boolean) {
    const products = [];
    order.products.forEach((item: any) => {
      products.push({id: item.id, quantity: item.quantity});
    });
    StorageService.insert(DB_DATATYPES.OBJECT, STORAGE_KEYS.ORDERS, order, order.id);
    products.forEach((prod) => {
      const data: any = StorageService.select(STORAGE_KEYS.ITEMS, prod.id);
      if (status) {
        data.quantity = (Number(data.quantity) - Number(prod.quantity));
      } else {
        data.quantity = (Number(data.quantity) + Number(prod.quantity));
      }
      StorageService.insert(DB_DATATYPES.OBJECT, STORAGE_KEYS.ITEMS, data, prod.id);
    });
  }
}
