import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'home',
    pathMatch: 'full'
  },
  {
    path: 'home',
    loadChildren: './shop/shop.module#ShopPageModule'
  },
  {
    path: 'cart',
    loadChildren: './cart/cart.module#CartPageModule'
  },
  {
    path: 'my-orders',
    loadChildren: './my-orders/my-orders.module#MyOrdersPageModule'
  },
  {
    path: 'admin-login',
    loadChildren: './admin-login/admin-login.module#AdminLoginPageModule'
  },
  {
    path: 'admin-home',
    loadChildren: './admin-home/admin-home.module#AdminHomePageModule'
  },
  {
    path: 'admin-orders',
    loadChildren: './admin-orders/admin-orders.module#AdminOrdersPageModule'
  },
  {
    path: 'admin-payment/:id',
    loadChildren: './admin-payment/admin-payment.module#AdminPaymentPageModule'
  },
  {
    path: 'my-order/:id',
    loadChildren: './my-order/my-order.module#MyOrderPageModule'
  },
  {
    path: 'admin-products',
    loadChildren: './admin-products/admin-products.module#AdminProductsPageModule'
  },
  {
    path: 'admin-add-product',
    loadChildren: './admin-add-product/admin-add-product.module#AdminAddProductPageModule'
  },
  {
    path: '**',
    redirectTo: 'home',
    pathMatch: 'full'
  },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })
  ],
  exports: [RouterModule]
})
export class AppRoutingModule {}
