import {Injectable} from '@angular/core';
import {DB_DATATYPES, STORAGE_KEYS} from './constants.service';

@Injectable({
  providedIn: 'root'
})
export class StorageService {

  static init() {
    if (StorageService.get(STORAGE_KEYS.ADMIN) === false) {
      StorageService.set(STORAGE_KEYS.ADMIN, { username: 'admin', password: 'password' });
    }

    if (StorageService.get(STORAGE_KEYS.ORDERS) === false) {
      StorageService.set(STORAGE_KEYS.ORDERS, {});
    }

    if (StorageService.get(STORAGE_KEYS.CART) === false) {
      StorageService.set(STORAGE_KEYS.CART, []);
    }

    if (StorageService.get(STORAGE_KEYS.ITEMS) === false) {
      StorageService.set(STORAGE_KEYS.ITEMS, {});
    }
  }

  static set(key: STORAGE_KEYS, value: any) {
    localStorage.setItem(key, JSON.stringify(value));
  }

  static get(key: STORAGE_KEYS): boolean|any {
    const data = localStorage.getItem(key);

    if (data === null) {
      return false;
    } else if (data.charAt(0) === '[' || data.charAt(0) === '{' || data.charAt(0) === '"') {
      return JSON.parse(data);
    } else {
      return data;
    }
  }

  static drop(key: STORAGE_KEYS) {
    localStorage.removeItem(key);
  }

  static insert(objectType: DB_DATATYPES, dbKey: STORAGE_KEYS, data: any, objectKey?: string): boolean {
    const dbData: any[] = StorageService.get(dbKey);
    if (objectType === DB_DATATYPES.OBJECT) {
      dbData[objectKey] = data;
    } else if (objectType === DB_DATATYPES.ARRAY) {
      dbData.push(data);
    }
    StorageService.set(dbKey, dbData);
    return true;
  }

  static delete(objectType: DB_DATATYPES, dbKey: STORAGE_KEYS, index: number|any) {
    const dbData: any[] = StorageService.get(dbKey);
    if (objectType === DB_DATATYPES.OBJECT) {
      delete dbData[index];
    } else if (objectType === DB_DATATYPES.ARRAY) {
      dbData.splice(index, 1);
    }
    StorageService.set(dbKey, dbData);
    return true;
  }

  static select(dbKey: STORAGE_KEYS, objectKey: string): boolean|any {
    const dbData: [any] = StorageService.get(dbKey);
    if (dbData[objectKey] === undefined) {
      return false;
    } else {
      return dbData[objectKey];
    }
  }

}
